@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  background-image: linear-gradient(#1e1922, #143f70ee), url(/img/bg.jpg);
  font-family: 'Abel', sans-serif;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}
p {
    text-indent: 2rem;
}

.container,
.container-fluid {
    color: whitesmoke;
    font-family: 'Abel', cursive;
}

#intro_title {
    font-weight: 800;
    margin-right: 250px;
}

#intro_text {
    font-weight: 500;
    font-size: 1.2rem;
    margin-left: 25px;
}

#intro_img {
    position: absolute;
    height: 500px;
    width: 700px;
    margin-right: 20%;
    float: right;
    padding-left: 0px;
    padding-right: 0px;
    right: 0;
}

#textbox {
    border-radius: 10px;
    padding: 10px;
    position: absolute;
    margin-left: 5%;
    margin-top: 5%;
    height: 350px;
    width: 500px;
    z-index: 1;
}

#textbox,
#textbox_mobile {
    background-color: #2e2933ec;
    border: 1px solid #043a64bd;
    box-shadow: -1px 0 8px -2px #000000;
}

#icon-git {
    height: 44px;
    width: 40px;
    -webkit-filter: invert(100%);
            filter: invert(100%);
    z-index: 3;
    margin-right: 10px;
}

#icon-lnki {
    height: 48px;
    width: 40px;
    -webkit-filter: invert(100%);
            filter: invert(100%);
    z-index: 3;
    margin-right: 10px;
}

#icon-res {
    height: 40px;
    width: 32px;
    -webkit-filter: invert(100%);
            filter: invert(100%);
    z-index: 3;
}

#intro_section {
    height: 80vh;
}

#about_me {
    background-color: #2e2933d3;
    border: 1px solid #043a64bd;
    box-shadow: -1px 0 8px -2px #000000;
    border-radius: 10px;
}

#intro_img,
#intro_img_mobile {
    background-color: #2e2933d3;
    border: 1px solid #043a64bd;
    box-shadow: -1px 0 20px -2px #000000;
    border-radius: 10px;
}

#intro_section_mobile {
    display: none;
}

#about_img {
    width: 700px;
    border: 1px solid #043a64bd;
    border-radius: 10px;
    box-shadow: 5px 0 10px -2px #000000;
    float: left;
    margin-right: 5%;
    margin-top: 5%;
    margin-bottom: 5%;
}

#intro_img_mobile {
    width: 150px;
    float: right;
}

@media only screen and (max-width: 1400px) and (min-width: 1201px) {

    #intro_section {
        display: none;
    }

    #intro_img {
        display: none;
    }

    #about_img {
        width: 100%;
    }

    #intro_section_mobile {
        display: block;
        width: 65%;
    }

    #intro_text_mobile {
        margin-left: 5%;
        margin-right: 5%;
    }

    #icon-lnki,
    #icon-git,
    #icon-res {
        height: 60px;
        width: 50px;
    }

    #icon-git {
        margin-right: 20px;
    }

    #icon-lnki {
        margin-right: 20px;
    }
}

@media only screen and (max-width: 1200px) {

    #intro_section {
        display: none;
    }

    #intro_img {
        display: none;
    }

    #about_img {
        width: 100%;
    }

    #intro_section_mobile {
        display: block;
        width: 100%;
    }

    #about_img {
        width: 50%;
    }
}
#projects_accordion .accordion-item {
    background-color: #2e293385;
    border: none;
    box-shadow: 0 4px 8px 0 #000000, 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin-bottom: 10px;
}

#projects_accordion .accordion-button {
    color: #043a64bd;
}

#projects_accordion .accordion-body {
    color: #ffffff;
}

#projects_accordion .accordion-button {
    color: #ffffff;
    background: linear-gradient(to right, #043a64bd, #2e2933d3);
    border-radius: 5px;
    transition: background-color 0.5s ease;
}

#projects_accordion .accordion-button:hover {
    background: linear-gradient(to right, #2e2933d3, #043a64bd);
}

.accordion-button::after {
    -webkit-filter: grayscale(1) invert(1);
    filter: grayscale(1) invert(1);
}

.accordion-button[aria-expanded="true"]::after {
    -webkit-filter: grayscale(1) invert(0.5);
    filter: grayscale(1) invert(0.5);
}

.iconImg {
    width: 60px;
    height: 60px;
}

.imgCol {
    padding: 0;
    margin: 0;
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title {
    float: right;
}

.github_url {
    color: #ffffff;
    font-size: 20px;
}

.academic_url {
    color: #ffffff;
    font-size: 20px;
}

#video_wrapper {
    width: auto;
    height: auto;
}

@media (max-width: 768px) {
    .iconImg {
        width: 40px;
        height: 40px;
    }

    #video_wrapper {
        width: 300px;
        height: auto;
    }
}
.scrollToTopBtn {
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 150px;
    height: 75px;
    color: #fff;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

@media (max-width: 768px) {
    .scrollToTopBtn {
        width: 100px;
        height: 50px;
        bottom: auto;
        top: 80px;
        left: 20px;
        right: auto;
        font-size: 0.9rem;
        opacity: 0.8;
    }

    #backtotop {
        display: none;
    }
}
  #navbar_bg {
    background-image: radial-gradient(circle, #2e2933 0%, #1E1922 100%);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  }

  #navbar_title {
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    font-family: 'Abel', cursive;
    padding-left: 5px;
    margin-left: 1%;
  }

  .nav-link {
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-duration: 0.2s;
            animation-duration: 0.2s;
    font-family: 'Abel', cursive;
  }

  #nav-link1,
  #nav-link2,
  #nav-link3,
  #nav-link4 {
    -webkit-animation: fade 1.0s forwards;
            animation: fade 1.0s forwards;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    opacity: 0;
  }

  #nav-link1 {
    -webkit-animation-delay: 0.125s;
            animation-delay: 0.125s;
  }

  #nav-link2 {
    -webkit-animation-delay: 0.25s;
            animation-delay: 0.25s;
  }

  #nav-link3 {
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;

  }

  #nav-link4 {
    -webkit-animation-delay: 0.75s;
            animation-delay: 0.75s;
  }

  .navbar-nav .nav-item.active .nav-link {
    color: #3b97dd;
    background-color: rgba(209, 209, 209, 0.100);
    border-radius: 3px;
  }

  .navbar-dark .nav-item .nav-link {
    color: whitesmoke;
    font-weight: 800;
  }

  .navbar-brand {
    color: whitesmoke;
  }

  .navbar-dark .nav-item:hover .nav-link {
    background-color: rgba(209, 209, 209, 0.100);
    border-radius: 3px;
  }

  @-webkit-keyframes fade {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  @keyframes fade {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  @-webkit-keyframes dance {
    from {
      -webkit-transform: rotate(15deg);
              transform: rotate(15deg);
    }

    to {
      -webkit-transform: rotate(-15deg);
              transform: rotate(-15deg);
    }
  }

  @keyframes dance {
    from {
      -webkit-transform: rotate(15deg);
              transform: rotate(15deg);
    }

    to {
      -webkit-transform: rotate(-15deg);
              transform: rotate(-15deg);
    }
  }

  /* if screen width is less than or equal to 1400px */
  @media (max-width: 1400px) {
    #navbarTogglerDemo02 {
      margin-left: 3%;
    }
  }
